@import './vars.scss';

* {
  @extend %body_text;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.main {
  flex: 1 0 auto;
}

.footer {
  flex: 0 0 auto;
}

.container {
  margin: 0 auto;
  padding: 0 20px;
  max-width: 1200px;
}

.container-fluid {
  margin: 0 auto;
  padding: 0 20px;
  max-width: 1440px;
}

@media screen and (max-width: 420px) {
  .container {
    padding: 0 5px;
  }
}
